    border: unset;
    font-size: 25px;
    background-color: unset;
    color: #ffffff;
    margin: 6px 15px;
}

@media only screen and (min-width: 825px) {
    nav button.barsMenu {
        display: none;
    }
}

nav button.option, nav button.marca {
    background-color: unset;
    border: unset;
    color: rgb(255, 255, 255);
    text-decoration: none;
    padding: 6.5px 20px;
    font-size: 25px;
    font-family: 'Lato', sans-serif;
    text-shadow: rgb(37, 37, 37) 1px 1px 1px;
