@-webkit-keyframes snowflakes-fall{0%{top:-10%}100%{top:100%}}
@-webkit-keyframes snowflakes-shake{
    0%{
    -webkit-transform:translateX(0px);transform:translateX(0px)
  }
    50%{
    -webkit-transform:translateX(80px);
    transform:translateX(80px)
  }
    100%{
    -webkit-transform:translateX(0px);
    transform:translateX(0px)
  }
}
@keyframes snowflakes-fall{0%{top:-10%}100%{top:100%}}
@keyframes snowflakes-shake{
  0%{-webkit-transform:translateX(0px);transform:translateX(0px)
  }
  50%{-webkit-transform:translateX(80px);transform:translateX(80px)}100%{-webkit-transform:translateX(0px);transform:translateX(0px)
  }
}.snowflake{
  position:fixed;
  top:-10%;
  z-index:9999;
  -webkit-user-select:none;
  -ms-user-select:none;
  user-select:none;
  cursor:default;
  -webkit-animation-name:snowflakes-fall,snowflakes-shake;
  -webkit-animation-duration:20s,3s;
  -webkit-animation-timing-function:linear,ease-in-out;
  -webkit-animation-iteration-count:infinite,infinite;
  -webkit-animation-play-state:running,running;
  animation-name:snowflakes-fall,snowflakes-shake;
  animation-duration:20s,3s;
  animation-timing-function:linear,ease-in-out;
  animation-iteration-count:infinite,infinite;
  animation-play-state:running,running
}.snowflake:nth-of-type(0){
  left:1%;
  -webkit-animation-delay:0s,0s;
  animation-delay:0s,0s
}.snowflake:nth-of-type(1){
  left:10%;
  -webkit-animation-delay:2s,1s;
  animation-delay:2s,1s
}.snowflake:nth-of-type(2){
  left:20%;
  -webkit-animation-delay:12s,.5s;
  animation-delay:12s,.5s
}.snowflake:nth-of-type(3){
  left:30%;
  -webkit-animation-delay:8s,2s;
  animation-delay:8s,2s
}.snowflake:nth-of-type(4){
  left:40%;
  -webkit-animation-delay:4s,2s;
  animation-delay:4s,2s
}.snowflake:nth-of-type(5){
  left:50%;
  -webkit-animation-delay:16s,3s;
  animation-delay:16s,3s
}.snowflake:nth-of-type(6){
  left:60%;
  -webkit-animation-delay:12s,2s;
  animation-delay:12s,2s
}.snowflake:nth-of-type(7){
  left:70%;
  -webkit-animation-delay:5s,1s;
  animation-delay:5s,1s
}.snowflake:nth-of-type(8){
  left:80%;
  -webkit-animation-delay:2s,0s;
  animation-delay:2s,0s
}.snowflake:nth-of-type(9){
  left:90%;
  -webkit-animation-delay:6s,1.5s;
  animation-delay:6s,1.5s
}
/*PORTAFOLIO*/
main section div.contenedor-portafolio {
