main section div.proyectos ul li {
    width: 100%;
    height: auto;
    background-color: #3b93f7e7;
    text-align: center;
    margin: 20px 75px;
    float: left;
    border-radius: 3px;
    border: solid 2px #2a2a2a;
    overflow: hidden;
    -webkit-animation-name: tarjetas;
            animation-name: tarjetas;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes tarjetas {
    0% {
        background-color: #3B93F7;
    }
    50% {
        background-color: #5ea9ff;
    }
    100% {
        background-color: #3B93F7;
    }
}

